@use "sass:math";
// 1. Include functions first (so you can manipulate colors, SVGs, calc, etc)
@import "../../../../node_modules/bootstrap/scss/functions";

// 2. Include any default variable overrides here
@import "variables-custom.scss";

// 3. Include remainder of required Bootstrap stylesheets
@import "../../../../node_modules/bootstrap/scss/variables";
@import "../../../../node_modules/bootstrap/scss/mixins";
@import "../../../../node_modules/bootstrap/scss/list-group";
@import "../../../../node_modules/bootstrap/scss/modal";
@import "../../../../node_modules/bootstrap/scss/_popover";
@import "../../../../node_modules/bootstrap/scss/tooltip";
@import "svg-url-encoder.scss";
@import "components/caret";

#form-generator-container {
    max-width: 1140px;
    #app main .card-body,
    #ui-right-column .card-body {
        height: 95vh;
    }
}

#app main .card-body {
  overflow-y: auto;
}

#ui-right-column .card-body {
  overflow-x: hidden;
  overflow-y: auto;
}

.list-group-item {
    width: 100%;
    margin: 5px 0 !important;
    border-width: 1px !important;
    border-radius: 0.25rem !important;
}

@media (min-width: 768px) {
    .list-group-item {
        width: 47%;
        margin: 5px 1.5% !important;
    }

    /* hide UI left / right column */

    #ui-left-column,
    #ui-right-column {
        -webkit-transition: all 200ms ease-in;
        -moz-transition: all 200ms ease-in;
        -ms-transition: all 200ms ease-in;
        -o-transition: all 200ms ease-in;
        transition: all 200ms ease-in;
    }

    #ui-left-column.ui-hidden {
        margin-left: -25%;
        opacity: 0;
    }

    #ui-right-column.ui-hidden {
        margin-right: -33.332%;
        opacity: 0;
    }
}

@media (min-width: 992px) {
    #ui-right-column.ui-hidden {
        margin-right: -25%;
    }
}

/* Main Form & Components
-------------------------------------------------- */

#fg-form-wrapper,
#fg-form-wrapper form {
    width: 100%;
    min-height: 65vh;
    align-content: flex-start;
}

#fg-form-wrapper form {
    section {
        width: 100%;
        border-left-style: solid;
        border-left-width: 5px;
        border-left-color: transparent;
        -webkit-transition: all 200ms ease-in;
        -moz-transition: all 200ms ease-in;
        -ms-transition: all 200ms ease-in;
        -o-transition: all 200ms ease-in;
        transition: all 200ms ease-in;
    }
    &:not(.form-horizontal) label[class*="col"],
    &:not(.form-horizontal) div[class*="col"] {
        flex: 0 0 100% !important;
        max-width: 100% !important;
    }
    .section-component > * {
        padding-top: 4px;
        padding-bottom: 4px;
    }
}

.section-component {
    align-items: start;
    margin-bottom: 1rem;
    > *:not(.section-tools) {
        flex-grow: 1;
    }
    .form-group {
        margin-bottom: 0 !important;
    }
    .btn-drag {
        min-width: 42px;
        &.btn-drag-move {
            cursor: move !important;
        }
        &.btn-drag-copy {
            cursor:copy !important;
        }
    }
    .section-tools {
        opacity: 0;
        -webkit-transition: all 100ms ease-in;
        -moz-transition: all 100ms ease-in;
        -ms-transition: all 100ms ease-in;
        -o-transition: all 100ms ease-in;
        transition: all 100ms ease-in;
    }
    &:hover .section-tools {
        opacity: 1;
    }
}

/* Main form section widths
-------------------------------------------------- */

.section-component {
    &.w-33 {
        width: 33.332% !important;
    }
    &.w-66 {
        width: 66.662% !important;
    }
    &.w-33,
    &.w-50,
    &.w-66 {
        position: relative;
        margin-top: 40px;
        padding-left: 8px;
        padding-right: 4px;
        .section-tools {
            position: absolute;
            bottom: 100%;
            padding-top: 0 !important;
            padding-bottom: 0 !important;
            &:first-of-type {
                left: -5px;
            }
            &:not(:first-of-type) {
                right: 0;
            }
        }
        button {
            padding-bottom: 0;
        }
    }
}

/* drag & drop
-------------------------------------------------- */

#sidebar-components li[aria-grabbed="true"] {
    display: none !important;
}

#sidebar-components li {
    cursor: grab;
}

#droppable-hover-template {
    border: 1px dashed #ccc;
}

/* Component Settings (right column)
-------------------------------------------------- */

#components-settings-form {
    fieldset {
        position: relative;
        .btn-delete {
            position: absolute;
            top: -2px;
            right: 0;
        }
        &.collapsed {
            padding-top: 0 !important;
            background-color: transparent !important;
            border: none !important;
            .btn-delete {
                top: -1.5rem;
            }
        }
    }
    .checkbox-inline,
    .radio-inline,
    .checkbox label,
    .radio label,
    .form-check-label {
        margin-right: 20px !important;
        padding-left: 24px !important;
    }
}

#user-form-settings-plugins,
#components-settings-form {
    legend {
        float: none !important;
    }
}

/* Misc
-------------------------------------------------- */

.badge-primary,
.btn-primary {
    text-decoration: none;
    background-color: $primary;
    color: white;
}

a.badge-primary:hover,
a.btn-primary:hover,
a.badge-secondary:hover,
a.btn-secondary:hover {
    color: white;
    &.badge-primary,
    &.btn-primary {
        background-color: darken($primary, 20);
    }
    &.badge-secondary,
    &.btn-secondary {
        background-color: darken($secondary, 20);
    }
}

.badge-secondary,
.btn-secondary {
    text-decoration: none;
    background-color: $gray-700;
    color: white;
}

.border-yellow-500 {
    border-color: $yellow-500 !important;
}

.bg-yellow-100 {
    background-color: $yellow-100;
}

.bg-gray-300 {
    background-color: $gray-300;
}

.bg-gray-500 {
    background-color: $gray-500;
}

button[data-toggle="collapse"][aria-expanded="true"]:after {
    -moz-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}

.form-control .ss-content {
    margin-top: calc(0.375rem + 1px);
    margin-left: -0.75rem;
}

.lcs_main_wrap > label {
    display: none !important;
}

.list-unstyled-item {
    list-style-type: none !important;
}

#preview-modal {
    overflow-y: hidden !important;
}

.modal-xl {
    max-width: 96% !important;
    .modal-content {
        min-height: 95vh !important;
        .modal-body {
            height: 100%;
        }
    }
}

.nowrap {
    white-space: nowrap;
}

.phpfb-centered {
    display: flex !important;
    flex-grow: 0 !important;
    flex-shrink: 0 !important;
    flex-basis: 0% !important;
    justify-content: center;
    width: fit-content;
    margin-left: auto !important;
    margin-right: auto !important;
    margin-bottom: 1.5rem;
}

.phpfb-centered button {
    white-space: nowrap;
}

.phpfb-centered .col {
    flex-basis: auto !important;
    margin-left: initial !important;
}

.phpfb-centered > div:not(.lcs_wrap) {
    width: fit-content !important;
}

.phpfb-centered div.grid {
    display: flex;
}

.phpfb-centered-stacked {
    flex-direction: column;
    align-items: center;
}

.phpfb-centered-stacked label {
    margin-left: auto;
    margin-right: auto;
}

.pretty-checkbox-fieldset:not(.active) {
    display: none;
}

.select2-container--default .select2-selection--multiple .select2-selection__choice {
    background-color: #e2e0dd !important;
    border: 1px solid #e2e0dd !important;
    color: #212529 !important;
}

.select2-container--default .select2-selection--multiple .select2-selection__choice__remove {
    color: #212529 !important;
}

.vanilla-js,
.jquery {
    position: relative;
    &:after {
        content: " ";
        position: absolute;
        top: -0.15rem;
        right: 0.2rem;
        width: 1.5rem;
        height: 1.5rem;
        background-size: cover;
        background-repeat: no-repeat;
    }
}

.vanilla-js:after {
    background-image: url("../images/javascript.svg");
}

.jquery:after {
    background-image: url("../images/jquery.svg");
}

/* Toasts
-------------------------------------------------- */
.tata.success {
    font-weight: 300;
    background: #0f9e7b !important;
}

.tata-title {
    font-weight: 400;
    padding-bottom: 10px;
}
