/**
 *
 * Custom variables
 *
 */

//@import '../../../../node_modules/bootstrap/scss/variables';
//@import '../../../../node_modules/bootstrap/scss/mixins';
//@import '../../../../node_modules/bootstrap/scss/utilities';

$font-family-sans-serif: Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
$font-family-base: $font-family-sans-serif;
$font-family-monospace: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
$font-family-code: $font-family-monospace;

$spacer: 1rem;
$spacers: ();
$spacers: map-merge(
    (
        0: 0,
        1: (
            $spacer * 0.25,
        ),
        2: (
            $spacer * 0.5,
        ),
        3: $spacer,
        4: (
            $spacer * 1.5,
        ),
        5: (
            $spacer * 3,
        ),
        6: (
            $spacer * 6.25,
        ),
        7: (
            $spacer * 12.5,
        ),
    ),
    $spacers
);

$white: #fff;
$gray-100: #f6f6f5;
$gray-200: #eae8e5;
$gray-300: #d4d1cc;
$gray-400: #bfbab2;
$gray-500: #a9a398;
$gray-600: #7f7a72;
$gray-700: #55524c;
$gray-800: #2a2926;
$gray-900: #191817;
$black: #000;

$blue: #0e73cc;
$indigo: #3f51b5;
$red: #fc4848;
$pink: #e6006f;
$orange: #fd7e14;
$yellow: #ffc107;
$green: #0f9e7b;
$cyan: #00c2db;
$gray: #8c8476;
$gray-dark: #38352f;

$bs4: #0e73cc;
$bs5: #3f51b5;
$bulma: #00d1b2;
$foundation: #1779ba;
$material-bootstrap: #e85183;
$material: #e8797c;
$tailwind: #38bdf8;
$uikit: #73bbff;

$colors: (
    "blue": $blue,
    "red": $red,
    "pink": $pink,
    "yellow": $yellow,
    "gray": $gray,
    "gray-dark": $gray-dark,
);

$primary: $blue;
$secondary: $gray-600;
$success: $green;
$info: $cyan;
$warning: $yellow;
$danger: $red;
$light: $gray-100;
$dark: $gray-900;

$theme-colors: (
    "primary": $primary,
    "secondary": $secondary,
    "success": $success,
    "info": $info,
    "pink": $pink,
    "warning": $warning,
    "danger": $danger,
    "light": $light,
    "dark": $dark,
);

$font-size-base: 0.9375rem;
$font-size-lg: ($font-size-base * 1.25);
$font-size-sm: ($font-size-base * 0.875);

$h1-font-size: $font-size-base * 2.6666666666666665;
$h2-font-size: $font-size-base * 2;
$h3-font-size: $font-size-base * 1.75;
$h4-font-size: $font-size-base * 1.5;
$h5-font-size: $font-size-base * 1.25;
$h6-font-size: $font-size-base;

$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-bold: 500;

$font-weight-base: $font-weight-normal;
$dt-font-weight: $font-weight-bold;
$nested-kbd-font-weight: $font-weight-bold;

$badge-font-weight: $font-weight-bold;

$alert-link-font-weight: $font-weight-bold;
$close-font-weight: $font-weight-bold;

$enable-shadows: true;

$body-color: #2a2d2d;
$body-light-color: $gray-400;

$btn-close-color: $body-color;
$btn-close-bg: url("data:image/svg+xml,%3Csvg xmlns='https://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%232a2d2d'%3E%3Cpath d='M.293.293a1 1 0 011.414 0L8 6.586 14.293.293a1 1 0 111.414 1.414L9.414 8l6.293 6.293a1 1 0 01-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 01-1.414-1.414L6.586 8 .293 1.707a1 1 0 010-1.414z'/%3E%3C/svg%3E");
// transparent url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='https://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%232a2d2d'%3E%3Cpath d='M.293.293a1 1 0 11.414 0 NaN NaNlNaN NaN NaN NaNa1 1 0 111.414 1.414 NaN NaNlNaN NaN NaN NaNa1 1 0 1-1.414 NaN NaNlNaN NaN NaN NaNa1 1 0 1-1.414 NaN NaNlNaN NaN NaN NaNa1 1 0 10-1.414 NaN NaNz'/%3E%3C/svg%3E") 50%/1em auto no-repeat
$navbar-nav-link-padding-x: 1rem;
$navbar-brand-padding-y: 0;
$navbar-light-active-color: map-get($colors, danger-500);
$navbar-dark-color: rgba(255, 255, 255, 0.65);

$alert-border-radius: 0;
$badge-border-radius: 0;
$btn-border-radius: 0;
$btn-border-radius-sm: 0;
$btn-border-radius-lg: 0;
$card-border-radius: 0;
$input-border-radius: 0;
$input-border-radius-sm: 0;
$input-border-radius-lg: 0;
$nav-pills-border-radius: 0;

$enable-negative-margins: true;

$transition-collapse: height 0.35s ease !important;

$table-bg-scale: -60%;

$table-variants: (
    "primary": shift-color($primary, $table-bg-scale),
    "secondary": shift-color($secondary, $table-bg-scale),
    "success": shift-color($success, $table-bg-scale),
    "info": shift-color($info, $table-bg-scale),
    "warning": shift-color($warning, $table-bg-scale),
    "danger": shift-color($danger, $table-bg-scale),
    "light": $light,
    "dark": $gray-700,
);
